import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import { TransitionProps } from "@mui/material/transitions";
import { TAllUser } from "modules/admin/adminType";
import * as React from "react";
import { useTranslation } from "react-i18next";

import { AdminEditUserForm } from "../forms/admin-edit-user-form";

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export const AdminEditUserDialog = ({ userInfo }: { userInfo: TAllUser }) => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const { t } = useTranslation();
  return (
    <>
      <Button onClick={handleClickOpen}>{t("admin.users.editPassword")}</Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{t("admin.users.editPassword")}</DialogTitle>
        <DialogContent>
          {open && (
            <AdminEditUserForm
              onClose={() => setOpen(false)}
              defaultValues={{
                _id: userInfo._id,
                name: userInfo.name,
                email: userInfo.email,
                role: userInfo.role,
                picture: userInfo.picture,
              }}
            />
          )}
        </DialogContent>
      </Dialog>
    </>
  );
};
