import { yupResolver } from "@hookform/resolvers/yup";
import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  TextField,
} from "@mui/material";
import { Box } from "@mui/system";
import { queryClient } from "index";
import { editUserInformation } from "modules/admin/adminFetch";
import { Controller, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { useMutation } from "react-query";
import * as yup from "yup";

// import { editAdmin } from "../superFetch";
export type AdminEditFields = {
  _id: string;
  name: string;
  email: string;
  role: string;
  picture: string;
};

export const AdminEditUserForm = ({
  onClose,
  defaultValues,
}: {
  onClose: () => void;
  defaultValues: Record<string, any>;
}) => {
  const schema = yup.object().shape({
    _id: yup.string().required(),
    name: yup.string().required(),
    email: yup.string().required(),
    role: yup.string().required(),
    picture: yup.string().required(),
  });
  const {
    control,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<AdminEditFields>({
    defaultValues: defaultValues,
    resolver: yupResolver(schema),
  });
  const $editUser = useMutation(editUserInformation);

  const onSubmit = (data: AdminEditFields) => {
    $editUser.mutate(
      { data },
      {
        onSuccess: (data) => {
          queryClient.invalidateQueries({ active: true });
          reset(defaultValues, { keepDirty: false });
          onClose();
        },
      }
    );
  };
  const { t } = useTranslation();
  return (
    <>
      <Stack
        justifyContent="center"
        width={1}
        alignItems="center"
        spacing={2}
        sx={{
          background: "background",
          maxWidth: { lg: "500px", xs: "300px" },
          borderRadius: 1,
          mt: 1,
        }}
      >
        <Box
          sx={{
            width: "100%",
          }}
        >
          <form onSubmit={handleSubmit(onSubmit)}>
            <Stack direction="column">
              <Grid container spacing={2}>
                <Grid item xs={12} lg={12}>
                  <Controller
                    control={control}
                    name="_id"
                    render={({ field }) => (
                      <TextField
                        fullWidth
                        label={t("admin.userQuiz.firstName")}
                        disabled
                        type="text"
                        required
                        error={!!errors._id}
                        {...field}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12} lg={12}>
                  <Controller
                    control={control}
                    name="name"
                    render={({ field }) => (
                      <TextField
                        fullWidth
                        label={t("login.fullName")}
                        type="text"
                        required
                        error={!!errors.name}
                        {...field}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Controller
                    control={control}
                    name="email"
                    render={({ field }) => (
                      <TextField
                        fullWidth
                        label={t("login.email")}
                        type="text"
                        required
                        error={!!errors.email}
                        {...field}
                      />
                    )}
                  />
                </Grid>

                <Grid item xs={12} lg={12}>
                  <Controller
                    control={control}
                    name="role"
                    render={({ field }) => (
                      <FormControl required fullWidth>
                        <InputLabel>{t("admin.users.role")}</InputLabel>
                        <Select
                          label={t("admin.users.role")}
                          required
                          error={!!errors.role}
                          {...field}
                        >
                          <MenuItem value="user">User</MenuItem>
                          <MenuItem value="admin">Admin</MenuItem>
                        </Select>
                      </FormControl>
                    )}
                  />
                </Grid>
              </Grid>

              <Stack direction="row" mt={2} gap={1}>
                <Button fullWidth type="submit" disabled={$editUser.isLoading}>
                  {t("admin.users.submit")}
                </Button>
                <Button
                  onClick={onClose}
                  fullWidth
                  variant="outlined"
                  color="error"
                >
                  {t("main.donate.cancel")}
                </Button>
              </Stack>
            </Stack>
          </form>
        </Box>
      </Stack>
    </>
  );
};
