import {
  Chip,
  Divider,
  Skeleton,
  TableCell,
  TableRow,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import { getAllUsers } from "modules/admin/adminFetch";
import { AdminDeleteUserDialog } from "modules/admin/users/dialogs/admin-delete-user-dialog";
import { AdminEditPasswordDialog } from "modules/admin/users/dialogs/admin-edit-password-dialog";
import { AdminEditUserDialog } from "modules/admin/users/dialogs/admin-edit-user-dialog";
import { GlobalPagination } from "other/global/pagination/pagination";
import { GlobalTablle } from "other/global/table/table";
import { MainLayout } from "other/layouts/mainLayout";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useQuery } from "react-query";

const AllUsers = () => {
  const [page, setPage] = useState(1);

  const { t } = useTranslation();
  const $allUsers = useQuery(`all-users-${page}`, () => getAllUsers({ page }));
  return (
    <MainLayout>
      <Box mb={2} width={1}>
        <Chip
          label={<Typography variant="h1">{t("header.allUsers")}</Typography>}
        />
        <Divider />
      </Box>
      {$allUsers.isLoading && <Skeleton width="100%" height={500} />}
      {$allUsers.data && (
        <>
          <GlobalTablle
            head={[
              {
                label: "â„–",
                id: "id",
              },
              {
                label: t("admin.users.registrationTime"),
                id: "firstName",
              },
              {
                label: t("login.fullName"),
                id: "lastName",
              },
              {
                label: t("login.email"),
                id: "email",
              },
              {
                label: t("admin.users.role"),
                id: "role",
              },
              {
                label: "",
                id: "edit",
              },
              {
                label: "",
                id: "editPassword",
              },
              {
                label: "",
                id: "delete",
              },
            ]}
            body={$allUsers.data.data.map((user, index) => {
              return (
                <TableRow
                  key={user._id}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    {index + 1 + (page - 1) * 10}
                  </TableCell>
                  <TableCell component="th" scope="row">
                    {user.createdAt.split("T")[0]} -{" "}
                    {user.createdAt.split("T")[1].split(".")[0]} (UTC)
                  </TableCell>
                  <TableCell component="th" scope="row">
                    {user.name}
                  </TableCell>
                  <TableCell component="th" scope="row">
                    {user.email}
                  </TableCell>
                  <TableCell>{user.role}</TableCell>
                  <TableCell>
                    <AdminEditUserDialog userInfo={user} />
                  </TableCell>
                  <TableCell>
                    <AdminEditPasswordDialog _id={user._id} />
                  </TableCell>
                  <TableCell>
                    <AdminDeleteUserDialog _id={user._id} name={user.name} />
                  </TableCell>
                </TableRow>
              );
            })}
          />
          <GlobalPagination
            page={page}
            pages={$allUsers.data.totalPages}
            setPage={setPage}
          />
        </>
      )}
    </MainLayout>
  );
};
export default AllUsers;
