import { yupResolver } from "@hookform/resolvers/yup";
import { Button, Grid, Stack, TextField } from "@mui/material";
import { Box } from "@mui/system";
import { queryClient } from "index";
import { editUserPassword } from "modules/admin/adminFetch";
import { Controller, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { useMutation } from "react-query";
import * as yup from "yup";

export type ChangePasswordFields = {
  password: string;
  passwordConfirm?: string;
};

export const AdminEditPasswordForm = ({
  onClose,
  defaultValues,
  _id,
}: {
  onClose: () => void;
  defaultValues: Record<string, any>;
  _id: string;
}) => {
  const schema = yup.object().shape({
    password: yup.string().required().min(8, "Must be at lease 8 symbols"),
    passwordConfirm: yup
      .string()
      .oneOf([yup.ref("password"), null], "Passwords does not match")
      .required(),
  });
  const {
    control,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<ChangePasswordFields>({
    defaultValues: defaultValues,
    resolver: yupResolver(schema),
  });
  const $editPassword = useMutation(editUserPassword);

  const onSubmit = (data: ChangePasswordFields) => {
    $editPassword.mutate(
      { _id, data },
      {
        onSuccess: (data) => {
          queryClient.invalidateQueries({ active: true });
          reset(defaultValues, { keepDirty: false });
          onClose();
        },
      }
    );
  };
  const { t } = useTranslation();
  return (
    <>
      <Stack
        justifyContent="center"
        width={1}
        alignItems="center"
        spacing={2}
        sx={{
          background: "background",
          maxWidth: { lg: "500px", xs: "300px" },
          borderRadius: 1,
          mt: 1,
        }}
      >
        <Box
          sx={{
            width: "100%",
          }}
        >
          <form onSubmit={handleSubmit(onSubmit)}>
            <Stack direction="column">
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Controller
                    control={control}
                    name="password"
                    render={({ field, fieldState: { error } }) => (
                      <TextField
                        fullWidth
                        label={t("login.password")}
                        type="password"
                        required
                        error={!!errors.password}
                        {...field}
                        helperText={error?.message}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Controller
                    control={control}
                    name="passwordConfirm"
                    render={({ field, fieldState: { error } }) => (
                      <TextField
                        fullWidth
                        label={t("login.rpassword")}
                        type="password"
                        required
                        error={!!errors.passwordConfirm}
                        {...field}
                        helperText={error?.message}
                      />
                    )}
                  />
                </Grid>
              </Grid>

              <Stack direction="row" mt={2} gap={1}>
                <Button
                  fullWidth
                  type="submit"
                  disabled={$editPassword.isLoading}
                >
                  {t("admin.users.submit")}
                </Button>
                <Button
                  onClick={onClose}
                  fullWidth
                  variant="outlined"
                  color="error"
                >
                  {t("main.donate.cancel")}
                </Button>
              </Stack>
            </Stack>
          </form>
        </Box>
      </Stack>
    </>
  );
};
