import { TTransactions } from "modules/main/mainType";
import { Request } from "other/request";

import { TEditUser, TUsers } from "./adminType";
import { ChangePasswordFields } from "./users/forms/admin-edit-password-form";
import { AdminEditFields } from "./users/forms/admin-edit-user-form";

export const getAllTransactions = async ({ page }: { page: number }) =>
  Request<TTransactions>(`/transactions?page=${page}`, "GET", null);
export const getAllUsers = async ({ page }: { page: number }) =>
  Request<TUsers>(`/admin/users?page=${page}`, "GET", null);
export const editUserInformation = async ({
  data,
}: {
  data: AdminEditFields;
}) => Request<TEditUser>(`/admin/users/${data._id}`, "PATCH", data);

export const editUserPassword = async ({
  _id,
  data,
}: {
  _id: string;
  data: ChangePasswordFields;
}) => Request<TEditUser>(`/admin/users/password/${_id}`, "PATCH", data);
export const deleteUser = async ({ _id }: { _id: string }) =>
  Request<TEditUser>(`/admin/users/${_id}`, "DELETE", null);
