import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import { TransitionProps } from "@mui/material/transitions";
import * as React from "react";
import { useTranslation } from "react-i18next";

import { AdminEditPasswordForm } from "../forms/admin-edit-password-form";

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export const AdminEditPasswordDialog = ({ _id }: { _id: string }) => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const { t } = useTranslation();
  return (
    <>
      <Button color="secondary" onClick={handleClickOpen}>
        {t("admin.users.editPassword")}
      </Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{t("admin.users.editPassword")}</DialogTitle>
        <DialogContent>
          <AdminEditPasswordForm
            onClose={() => setOpen(false)}
            defaultValues={{
              password: "",
              passwordConfirm: "",
            }}
            _id={_id}
          />
        </DialogContent>
      </Dialog>
    </>
  );
};
